*, *::before, *::after {
    box-sizing: border-box;
}

html {
    font-size: 18px;
}

body {
    font-family: $ff;
}

h1 {
    font-size: 2.2rem;
}

h2{
    font-size: 1.8rem;
}


p {
    color: $lightGray;
    line-height: 1.8em;
}

button {
    color: $white;
    border-radius: 30px;
    padding: 1em 2em;
    box-shadow: 0px 0px 3px rgba(0, 0, 0, .5);
    border: none;
    margin: .8em;
}

button:hover{
    cursor: pointer;
}

.button1 {
background: $cyan;
}

.button1:hover {
background: $cyan-active;
}

.button2 {
    background: $purple;
}

.button2:hover {
    background: $purple-active;
}